   RESET
================================ */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* ===============================
   CARD LAYOUT
================================ */
.card-wrapper {
    position: relative;
    display: inline-block;
    cursor: pointer;
    margin: 2vmin;
    border-radius: 8px;

    /* animation 安定化 */
    transition: transform 0.5s ease;
    will-change: transform;
    z-index: 1;
}

.card-wrapper.active {
    z-index: 100;
}

/* ===============================
   CARD BODY
================================ */
.card {
    width: 360px;
    height: 235px;
    border-radius: 8px;
    position: relative;
    z-index: 2;

    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center / cover no-repeat;
    box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.75);
    overflow: hidden;

    /* 子要素では transform しない（重要） */
}

/* ===============================
   CARD BACKGROUND
================================ */
.card-background {
    position: absolute;
    top: 5%;
    left: 20%;
    width: 300px;
    height: 200px;
    padding: 0.5rem;
    border-radius: 8px;

    background:
        linear-gradient(to top, rgba(0, 0, 0, 0.65), rgba(20, 20, 20, 0.4)),
        url(${___CSS_LOADER_URL_REPLACEMENT_1___}) center / cover no-repeat;

    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.6);

    opacity: 0;
    transition: opacity 0.5s ease;
    z-index: 1;
}

.card-wrapper.active .card-background {
    opacity: 1;
}

/* ===============================
   TEXT STYLES
================================ */
.card-header {
    margin-left: 2rem;
    margin-bottom: 0.8vmin;
}

.card-content {
    transition: opacity 0.5s ease;
}

.card-content h3 {
    font-size: clamp(2rem, 0.667rem + 1.78vw, 2.8rem);
    font-family: sans-serif;
    font-weight: 400;
    padding-left: 2rem;
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
}

.card-content p {
    opacity: 1;
    transition: opacity 0.5s ease;
}

.card-footer {
    font-size: clamp(1.8rem, 1.467rem + 0.44vw, 2rem);
    font-weight: 600;
    position: absolute;
    left: 2rem;
    bottom: 1.5rem;
    border-bottom: 0.5px solid;
}

.card-footer::after {
    content: "more -";
    font-size: clamp(1.2rem, 0.867rem + 0.44vw, 1.4rem);
    margin-left: 1rem;
}

/* ===============================
   ACTIVE STATE EFFECTS
================================ */
.card-wrapper.active .card-content,
.card-wrapper.active .card-header,
.card-wrapper.active .card-footer {
    opacity: 0.65;
}

/* ===============================
   MODAL CONTEXT SAFETY
================================ */
.modal .cd-layout {
    display: block;
}
